import request from '@/utils/request';
export function Login(data) {
  return request({
    url: 'enterprise/auth/login',
    method: 'post',
    data
  })
}


export function dictionaries(params) {
  return request({
    url: 'enterprise/status/list',
    method: 'get',
    params
  })
}


//修改密码
export function Police(data) {
  return request({
    url: 'enterprise/password', 
    method: 'put',
    data
  })
}

