import Vue from 'vue';
import qs from 'qs';
import axios from 'axios';
import store from '@/store';
import { getToken } from "@/utils/auth";
import { Dialog,Toast } from "vant";
import router from '../router';

// create an axios instance
const service = axios.create({

  crossDomain: true,
  // baseURL: process.env.NODE_ENV === 'development' ? "/api" : window.baseUrl,
  baseURL: window.baseUrl, // url = base url + request url
  // send cookies when cross-domain requests
  timeout: 20000, // request timeout
  // headers:{
  //   token:"Token " + getToken()
  // }
})


// request interceptor请求拦截器
service.interceptors.request.use(config => {
    // console.log(store.getters.token);
    // console.log(config);
    if (store.getters.token) {

      // config.headers['token'] = "Token " + getToken()
      config.headers['Authorization'] = "Token " + getToken();
    }
    if (/^application\/x-www-form-urlencoded/.test(config.headers['content-type'])) {
      config.data = qs.stringify(config.data)
    }

    return config
  },
  error => {
    // console.log(error);
    // do something with request error
    return Promise.reject(error)
  }
)

// response interceptor响应拦截器
service.interceptors.response.use(response => {

    const res = response.data;

    if (response.status==200&&res.code == 401) {
      Dialog.confirm({
        title: "提示",
        message: "登录已过期请重新登录",
      }).then(() => {
        store.dispatch('user/resetToken').then(() => {
          location.reload()
          
          router.push({
            path :"/login"
          })   
          
        })

      })
    }

    return res
  },
  error => {
    // console.log(error);
    

    // if (error == "Error: Request failed with status code 401") {
    //   Dialog.confirm({
    //     title: "提示",
    //     message: "登录已过期请重新登录",
    //   })
    //     .then(() => {
    //       store.dispatch('user/resetToken').then(() => {
    //         location.reload()
    //       })
    //     })
    //     .catch(() => { });
    //   return
    // }



    if (error == "Error: Request failed with status code 405") {
      // console.log(666);
      Dialog.confirm({
        title: "提示",
        message: "请求超时",
      })

      return
    }

    if (error == "Error: timeout of 1000ms exceeded" || error == "Error: Network Error") {

      Toast.fail('请求出错');
      console.log(666);
      return
    }
    // console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export default service


