//统一提示方便全局修改
const msg = (title,  icon='none',duration=1000, mask=false)=>{
	if(Boolean(title) === false){
		return;
	}
  Toast({
		message:title,
		duration,
		mask,
		icon
	});
} 
const img = "http://192.168.1.196:5001"
Vue.prototype.$img = img
Vue.prototype.$api = {msg, json:{}}; 

window.addEventListener('popstate', function (e) {
  router.isBack = true
},false)

Vue.use(Vant);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
