import Vue from 'vue'
import Router from 'vue-router'

import Layout from "@/layout/default"

Vue.use(Router)

export const constantRoutes = [
    {
        path: '/',
        component:Layout,
        redirect:"/dashboard",
        children: [
            {
                path: '/dashboard',
                name:"dashboard",
                component: () => import('@/views/dashboard/index'),
            }
        ]
    },{
        path: '/register',
        component:Layout,
        children:[{
            path: '/register/rooms',
            component:() => import('@/views/register/rooms'),
        },{
            path: '/register/roomDetail',
            component:() => import('@/views/register/roomDetail'),
        },{
            path: '/register/changeRoom',
            component:() => import('@/views/register/changeRoom'),
        },{
            path: '/register/createdhotel',
            name:"createdhotel",
            component: () => import('@/views/register/createdhotel'),
        },
        {
            path: '/register/createdparticulars',
            name:"createdparticulars",
            component: () => import('@/views/register/createdparticulars'),
        },
        {
            path: '/register/batchretreatroom',
            name:"batchretreatroom",
            component: () => import('@/views/register/batchretreatroom'),
        }
    ]
    },{
        path: '/login',
        name:"login",
        component: () => import('@/views/login/index')
    },
    {
        path: '/homepage',
        name:"homepage",
        component: () => import('@/views/homepage/privacy'),

    }
]

const createRouter = () => new Router({
    // mode: 'history', // require service support
    scrollBehavior: () => ({ y: 0 }),
    routes: constantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
    const newRouter = createRouter()
    router.matcher = newRouter.matcher // reset router
}

export default router